@charset "UTF-8";
@import url("https://fonts.googleapis.com/css?family=Inconsolata");
@import url("https://fonts.googleapis.com/css?family=PT+Mono");

%counter-margin {
  margin-top: -30px;
  margin-left: -30px;
  margin-right: -30px;
}

%headline-link {
  a {
    text-decoration: none;
    color: inherit;
    @each $state in (hover, focus) {
      &:#{$state} {
        color: var(--pfe-broadcasted--link--#{$state});
      }
    }
    &:visited {
      color: inherit;
    }
  }
}

%title {
  @extend %headline-link;
  font-size: 22px;
  margin-bottom: 10px;
  text-transform: uppercase;
  font-weight: 800;
  margin-top: 0;
  line-height: 1.4;
}

%card-title {
  @extend %headline-link;
  font-size: 14px;
  margin-bottom: 10px;
  text-transform: uppercase;
  font-weight: 800;
  margin-top: 0;
  line-height: 1.4;
}

%headline {
  @extend %headline-link;
  font-size: 32px;
  margin-bottom: 10px;
  font-weight: 800;
  margin-top: 0;
  line-height: 1.1;
  @media (min-width: 992px) {
    font-size: 39px;
  }
  @media (min-width: 1200px) {
    font-size: 42px;
  }
}

%headline-1 {
  @extend %headline-link;
  font-size: 24px;
  font-weight: 800;
  margin-top: 0;
  margin-bottom: 20px;
  line-height: 1.18;
}

%headline-2 {
  @extend %headline-link;
  font-size: 22px;
  font-weight: 800;
  margin-top: 0;
  margin-bottom: 20px;
  line-height: 1.18;
}

%headline-3 {
  @extend %headline-link;
  font-size: 20px;
  font-weight: 800;
  margin-top: 0;
  margin-bottom: 10px;
  line-height: 1.18;
}

%headline-4 {
  @extend %headline-link;
  font-size: 18px;
  font-weight: 600;
  margin-top: 0;
  margin-bottom: 10px;
  line-height: 1.18;
}

%headline-5 {
  @extend %headline-link;
  font-size: 16px;
  font-weight: 600;
  margin-top: 0;
  margin-bottom: 11px;
  line-height: 1.18;
}

%headline-6 {
  @extend %headline-link;
  font-size: 14px;
  font-weight: 600;
  margin-top: 0;
  line-height: 1.18;
}

%summary {
  font-size: 1.2em;
  line-height: 1.38;
  margin-top: 0;
  a {
    color: #dadada;
    @each $state in (hover, focus, visited) {
      &:#{$state} {
        color: var(--pfe-broadcasted--link--#{$state});
      }
    }
    &:visited {
      text-decoration: line-through;
    }
  }
}

%text-alignment-simple {
  [data-text-align="left"] & {
    text-align: left;
  }

  [data-text-align="right"] & {
    text-align: right;
  }

  [data-text-align="center"] & {
    text-align: center;
  }
}

%text-alignment {
  [data-text-align="right"] & {
    text-align: right;
  }
  [data-text-align="left--right"] & {
    text-align: left;
    @media (min-width: 768px) {
      text-align: right;
    }
  }
  [data-text-align="sm-md--center"] & {
    text-align: center;
    @media (min-width: 992px) {
      text-align: left;
    }
  }
}

%stacked {
  > * {
    margin-bottom: 30px;
    &:last-child {
      margin-bottom: 0;
    }
  }
}

%min-stacked {
  > * {
    margin-bottom: 15px;
    &:last-child {
      margin-bottom: 0;
    }
  }
}

@keyframes growBand {
  0% {
    opacity: 1;
    z-index: 1;
    min-height: 0;
  }

  50% {
    opacity: 0;
    z-index: 1;
    min-height: 550px;
  }

  100% {
    opacity: 0;
    z-index: -1;
    min-height: 550px;
  }
}

@keyframes shrinkBand {
  0% {
    opacity: 1;
    z-index: 1;
    max-height: 550px;
  }

  50% {
    opacity: 0;
    z-index: -1;
    max-height: 550px;
  }

  100% {
    opacity: 0;
    z-index: -1;
    max-height: 50px;
  }
}

@keyframes opacity {
  50% {
    opacity: 0;
    z-index: -1;
  }

  100% {
    opacity: 1;
    z-index: 1;
  }
}

:root {
  --primary: #252527;
  --secondary: #444;

  --background-color: var(--primary);
  --theme: dark;

  --title-color: #fffa;
  --headline-color: #252527;
  --text--color: #4c4c4c;
  --link--color: #062f4f;

  --pfe-theme--color--ui-accent: rgb(31, 73, 112);
  --pfe-theme--color--ui-accent--hover: rgb(49, 115, 176);
  --pfe-theme--color--ui-accent--focus: rgb(49, 115, 176);

  --pfe-theme--color--surface--accent: rgb(31, 73, 112);
  --pfe-theme--color--surface--accent--theme: saturated;
  --pfe-theme--color--surface--darker: var(--secondary, #444);

  --pfe-theme--container-spacer: 16px;
}

*,
*::after,
*:before {
  box-sizing: border-box;
}

html {
  backface-visibility: hidden;
}

body {
  margin: 0;
  font-size: 18px;
  line-height: 1.444;
  font-family: "Roboto", "Lato", "Helvetica Neue", Helvetica, Arial, serif;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

pfe-band {
  &[data-padding="half"] {
    --pfe-band--PaddingTop: 30px;
    --pfe-band--PaddingBottom: 30px;
  }
  &[data-padding="quarter"] {
    --pfe-band--Padding: 15px 0;
  }
  &[data-width="full"] {
    --pfe-band--Width: auto;
    --pfe-band--Padding--horizontal: 30px;
  }
  &[data-layout="angle"] {
    --pfe-band--Border: none;
    @media (min-width: 992px) {
      --pfe-band--Width: auto;
      [data-layout] {
        --pfe-theme--container-spacer: 0;
      }
      pfe-card {
        margin-top: calc(-2.15 * var(--pfe-band--PaddingTop));
        margin-bottom: calc(-2.15 * var(--pfe-band--PaddingBottom));
        margin-left: calc(-1 * var(--pfe-band--Padding--horizontal));
      }
    }
  }
}

pfe-card {
  &[data-type="custom"] {
    @media (min-width: 0) and (max-width: 991px) {
      overflow: visible;
      --pfe-card--BackgroundColor: white;
      --pfe-broadcasted--text: black;
    }
    @media (min-width: 992px) {
      --pfe-card--BorderRadius: 0;
      margin-top: calc(-1 * var(--pfe-band--PaddingTop, 30px));
      margin-bottom: calc(-1 * var(--pfe-band--PaddingBottom, 30px));
      clip-path: polygon(100% 0, 100% 0, 72% 100%, 0% 100%, 0 53%, 0% 0%);
      shape-outside: polygon(100% 0, 100% 0, 72% 100%, 0% 100%, 0 53%, 0% 0%);
      shape-margin: 10px;
      padding-right: 100px;
    }
  }
  &[data-padding="half"] {
    --pfe-card--Padding: 15px;
  }
  &[data-outline="border"] {
    --pfe-card--Border: 1px solid #252527;
    [data-theme~="dark"] & {
      --pfe-card--Border: 1px solid #fff;
    }
  }
  &[pfe-border] {
    --pfe-card--Border: 1px solid var(--pfe-theme--color--ui-border, #bbb);
  }
  &[tilt-on="hover"] {
    transform: rotate(1deg);
    transition: transform 0.1s ease-in;
    &:hover {
      transform: rotate(0deg);
    }
  }
}

pfe-cta {
  --pfe-theme--color--ui-accent: rgb(31, 73, 112);
  --pfe-theme--color--ui-accent--hover: rgb(49, 115, 176);
  --pfe-theme--color--ui-accent--focus: rgb(49, 115, 176);
}

.card-header {
  &-title,
  &-headline {
    @extend %text-alignment-simple;
  }
  &-title {
    @extend %card-title;
  }
  &-headline {
    @extend %headline-4;
  }
}

.band-header-title,
.header-title {
  @extend %text-alignment;
  @extend %title;
}

.band-header-headline,
.header-headline {
  @extend %text-alignment;
  @extend %headline;
}

.band-header-summary,
.header-summary {
  @extend %text-alignment;
  @extend %summary;
  .extra {
    font-size: 1.4em;
  }
}

.header > *:last-child {
  margin-bottom: 0;
}

.header-title[data-weight="thin"] {
  font-weight: 100;
}

.content {
  &[data-text-align="left"] {
    text-align: left;
  }
  &[data-text-align="right"] {
    text-align: right;
  }
  &[data-text-align="center"] {
    text-align: center;
  }
  img,
  embed,
  object,
  video {
    max-width: 100%;
    height: auto;
  }
  dt,
  th {
    color: var(--pfe-broadcasted--text, --headline-color);
  }
  p,
  pre,
  li,
  dd,
  td {
    color: var(--pfe-broadcasted--text, --text-color);
  }
  p {
    display: block;
    font-size: 1em;
    font-weight: normal;
    line-height: 1.44;
    margin: 0;
    &:not(:last-child) {
      margin-bottom: 1em;
    }
  }
  a {
    text-decoration: none;
    font-weight: 400;
    color: var(--pfe-broadcasted--link, --link--color);
    &:hover {
      text-decoration: underline;
      color: var(--pfe-broadcasted--link--hover, #002f3b);
    }
    &:focus {
      text-decoration: underline;
      color: var(--pfe-broadcasted--link--focus, #002f3b);
    }
    &:visited {
      text-decoration: line-through;
      color: var(--pfe-broadcasted--link--visited, #002f3b);
    }
    &[name]:not([href]) {
      color: inherit;
    }
  }
  @for $n from 1 through 6 {
    h#{$n} {
      @extend %headline-#{$n};
      color: var(--pfe-broadcasted--text, --headline-color);
    }
  }
  pre {
    font-family: "PT Mono", monospace;
    display: block;
    font-size: 1em;
    font-weight: normal;
    line-height: 1.44;
    margin: 0;
    &:not(:last-child) {
      margin-bottom: 1em;
    }
  }
  ul,
  ol {
    margin: 0;
    &:not(:last-child) {
      margin-bottom: 1.5em;
    }
  }
  li {
    font-size: 1em;
    margin: 0;
    line-height: 1.44;
  }
  dl {
    line-height: 1.2;
    margin: 0;
    &:not(:last-child) {
      margin-bottom: 1.5em;
    }
  }
  dt {
    margin-top: 0;
    font-size: 1em;
    font-weight: 600;
  }
  dd {
    margin: 0 0 1em 0;
    font-size: 1em;
  }
  th,
  td {
    font-size: 1em;
    padding: 10px 20px 10px 0;
    border-collapse: collapse;
    border: 0;
  }
  td {
    border-bottom: 1px solid;
  }
  b,
  strong {
    font-size: 1.2em;
    font-weight: 400;
  }
  .extra {
    font-size: 1.4em;
    font-weight: 900;
    line-height: 1;
  }
  > *:last-child {
    margin-bottom: 0;
  }
}

.video-embed {
  position: relative;
  &-title {
    @extend %headline-3;
  }
  &-summary {
    display: block;
    font-size: 1em;
    font-weight: normal;
    line-height: 1.44;
    margin: 0;
    &:not(:last-child) {
      margin-bottom: 1em;
    }
    &-link {
      text-decoration: none;
      font-weight: 400;
      &[name]:not([href]) {
        color: inherit;
      }
      &:hover,
      &:focus {
        text-decoration: underline;
      }
      &:visited {
        text-decoration: line-through;
      }
    }
  }
  &-details {
    @extend %stacked;
    &[data-state="visible"] {
      opacity: 1;
      z-index: 1;
      &[data-animation="video-play"] {
        animation: growBand 1s forwards ease-in;
      }
      &[data-animation="video-close"] {
        animation: shrinkBand 1s forwards ease-in;
      }
      &[data-state="hidden"] {
        position: absolute;
        top: 0;
        left: 0;
        z-index: -1;
        opacity: 0;
        width: 100%;
        &[data-animation="video-play"] {
          animation: opacity 0.5s 0.2s forwards ease-in;
        }
        &[data-animation="video-close"] {
          animation: opacity 0.5s forwards ease-in;
        }
      }
    }
  }
  &-media-container {
    margin-bottom: 1em;
    pfe-card & {
      @extend %counter-margin;
    }
  }
  &-iframe-container {
    padding: 0 0 56.25%;
    height: 0;
    position: relative;
    > * {
      width: 100%;
      height: auto;
      display: block;
    }
    iframe {
      left: 0;
      top: 0;
      height: 100%;
      width: 100%;
      position: absolute;
    }
    + h3 {
      margin-top: 20px;
    }
  }
}

.image-embed {
  &-container {
    width: 100%;
    [data-align="bleed"] & {
      margin-left: -30px;
      margin-right: -30px;
    }
    [data-align="bleed"]:first-child & {
      @extend %counter-margin;
    }
    [data-align="bleed"] &,
    [data-align="bleed"]:first-child & {
      background: inherit;
    }
    [data-img-shape="circle"] & {
      position: relative;
      perspective: 1000;
      transform-style: preserve-3d;
      @media (max-width: 991px) {
        max-width: 350px;
        margin: 0 auto;
      }
    }
  }
  &-img,
  &-svg {
    width: 100%;
    display: block;
    height: auto;
    [data-align="left"] & {
      margin-right: auto;
    }
    [data-align="right"] & {
      margin-left: auto;
    }
    [data-align="center"] & {
      margin-right: auto;
      margin-left: auto;
    }
    [data-align="full"] &,
    [data-align="bleed"] & {
      width: 100%;
      height: auto;
      display: block;
    }
    [data-scale="reduced"] & {
      transform: scale(0.75);
    }
  }
  &-img {
    .popup-link:hover & {
      cursor: zoom-in;
    }
    [data-img-hover="bw-color"] & {
      transition: all 200ms ease;
      filter: grayscale(0);
    }
    [data-img-hover="bw-color"]:hover & {
      filter: grayscale(100%);
    }
    [data-img-shape="circle"] & {
      border-radius: 50%;
    }
  }
  &-caption {
    display: block;
    font-size: 1em;
    font-weight: normal;
    line-height: 1.44;
    margin: 0;
    &:not(:last-child) {
      margin-bottom: 1em;
    }
  }
}

.social-icon {
  margin: 0 5px;
  &:last-child {
    margin-right: 0;
  }
  &:hover > * {
    color: #55acee;
  }
  > * {
    text-decoration: none;
    font-size: 22px;
  }
  > [name]:not([href]) {
    color: inherit;
  }
  > :visited {
    text-decoration: line-through;
  }
}

.cta-link {
  &[data-hover="special"] {
    position: relative;
    display: inline-block;
    outline: none;
    vertical-align: bottom;
    padding: 0 0.25em;
    color: #d04c3f;
    text-decoration: none;
    &:hover {
      color: #d04c3faa;
    }
  }
  [data-cta-type="video-play"] & {
    font-size: 1em;
    text-transform: uppercase;
    font-weight: 400;
    text-align: center;
    text-decoration: none;
    color: var(--pfe-broadcasted--link);
    &[data-size="sm"]::after {
      padding: 8px 0px 0px 3px;
      font-size: 12px;
      line-height: 10px;
      width: 25px;
      height: 25px;
      border-width: 1px;
    }
  }
}

.quote {
  quotes: "" "" "" "";
  &-container {
    display: flex;
    flex-flow: row nowrap;
    padding: 0;
    margin: 0 0 0 2em;
    list-style: none;
    + pfe-cta {
      font-style: normal;
    }
    &:before {
      content: open-quote;
      margin-top: -0.4em;
      font-size: 6em;
      height: 1em;
      padding-right: 0.1em;
      @media (max-width: 991px) {
        color: var(--pfe-theme--color--ui-accent);
        margin-left: -0.6em;
      }
      @media (min-width: 992px) {
        font-size: 9em;
        color: white;
      }
    }
  }
  &-attribution {
    font-weight: bold;
    text-align: right;
    a {
      color: inherit;
      &:hover {
        color: #fffa;
      }
    }
    @media (min-width: 992px) {
      [data-type="custom"] & {
        margin-right: 17%;
      }
    }
  }
  &-quotation {
    font-family: "Inconsolata", "Roboto", "Lato", "Helvetica Neue", Helvetica,
      Arial, serif;
    margin-top: 0;
    line-height: 1.4;
    font-style: italic;
    font-size: 1.4em;
    margin: 0;
    @media (min-width: 768px) {
      font-size: 1.6em;
    }
    @media (min-width: 992px) {
      font-size: 1.8em;
      [data-type="custom"] & {
        margin-right: 3%;
      }
    }
  }
  &-reference {
    font-size: pem(14);
    line-height: 1.142;
    margin: 0;
    text-transform: uppercase;
  }
  pfe-card > & {
    @media (min-width: 992px) {
      margin-left: var(--pfe-band--Padding--vertical);
    }
  }
}

.group {
  [data-layout="angle"] & {
    @media (max-width: 991px) {
      text-align: right;
      border-right: 4px solid var(--pfe-theme--color--ui-accent);
      padding-right: 30px;
    }
  }
}

// [data-layout]::after {
//     clear: both;
//     content: "";
//     display: table;
// }

[data-layout="6 6"] {
  margin-top: -30px;
  @media (min-width: 768px) {
    display: flex;
    flex-flow: row wrap;
    @supports (display: grid) {
      display: grid;
      grid-gap: var(--pfe-theme--container-spacer);
      grid-template-columns: repeat(2, 1fr);
    }
  }
  > * {
    width: 100%;
    float: left;
    margin-left: 0;
    margin-right: 0;
    margin-top: 30px;
    display: flex;
    flex-flow: column wrap;
    justify-content: center;
  }
}

[data-layout="4 4 4"] {
  margin-top: -30px;
  @media (min-width: 768px) {
    display: flex;
    flex-flow: row wrap;
  }
  > * {
    width: 100%;
    float: left;
    margin-left: 0;
    margin-right: 0;
    margin-top: 30px;
    @media (min-width: 768px) {
      &:nth-child(3n + 1) {
        clear: both;
        width: 31.61984%;
        float: left;
        margin-right: 2.57023%;
      }
      &:nth-child(3n + 2) {
        clear: both;
        width: 31.61984%;
        float: left;
        margin-right: 2.57023%;
      }
      &:nth-child(3n + 3) {
        clear: both;
        width: 31.61984%;
        float: right;
        margin-right: 0;
      }
    }
  }
}

[data-layout="8 4"] {
  margin-top: -30px;
  display: flex;
  flex-flow: row wrap;
  @supports (display: grid) {
    display: grid;
    grid-gap: 40px;
    grid-template-columns: 1fr;
    margin-top: 0;
    @media (min-width: 992px) {
      grid-gap: var(--pfe-theme--container-spacer, 16px);
      grid-template-columns: 8fr 4fr;
    }
  }
  > * {
    width: 100%;
    margin-left: 0;
    margin-right: 0;
    margin-top: 30px;
    @supports (display: grid) {
      margin-top: 0;
    }
  }
}

[data-layout="auto-grid"] {
  margin-top: -30px;
  @supports (display: grid) {
    display: flex;
    flex-flow: row wrap;
    display: grid;
    grid-template-columns: 1fr;
    grid-row-gap: 20px;
    grid-column-gap: 40px;
    margin-top: 0;
    @media (min-width: 768px) {
      grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    }
  }
  > * {
    width: 100%;
    margin-top: 30px;
    @supports (display: grid) {
      margin-top: 0;
    }
  }
}

[data-layout="stacked"] {
  @extend %stacked;
}

[data-layout="min-stacked"] {
  @extend %min-stacked;
}

[data-align="right"] {
  justify-content: flex-end;
}

[data-align="center"] {
  justify-content: center;
}

[data-align="both"] {
  display: flex;
  align-items: center;
  justify-content: center;
}

[data-layout="gallery5"] {
  display: flex;
  flex-flow: row wrap;
  display: grid;
  grid-gap: var(--pfe-theme--container-spacer, 16px);
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  pfe-cta > a {
    flex-grow: 1;
  }
}

[data-layout="space-between"] {
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
  @media (min-width: 768px) {
    flex-flow: row nowrap;
    justify-content: space-between;
  }
  > * {
    margin-bottom: 10px;
    @media (min-width: 768px) {
      &:first-child {
        flex-grow: 1;
      }
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
}

[data-img-shape="custom"] {
  min-width: 200px;
  max-width: 350px;
  @media (min-width: 0) and (max-width: 991px) {
    border-radius: 50%;
    overflow: hidden;
    margin: 0 auto 15px;
    min-height: 200px;
    max-height: 350px;
  }
  @media (min-width: 992px) {
    clip-path: polygon(
      5% 5%,
      100% 0%,
      100% 75%,
      75% 75%,
      75% 100%,
      50% 75%,
      0% 75%
    );
    shape-outside: polygon(
      5% 5%,
      100% 0%,
      100% 75%,
      75% 75%,
      75% 100%,
      50% 75%,
      0% 75%
    );
    shape-margin: 5px;
    float: left;
    margin: 0 15px 15px 0;
  }
}

.slider {
  &[data-align="bleed"] {
    @extend %counter-margin;
    width: auto;
  }
  &-nav {
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    justify-content: center;
    [data-nav-style="arrows"] & {
      height: 100%;
    }
    &-item {
      cursor: pointer;
      list-style-type: none;
      transition-property: background 0.2s ease-in 0s;
      [data-nav-style="text"] & {
        padding: 5px 15px;
        @media (min-width: 768px) {
          padding: 9px 40px;
        }
      }
      [data-nav-style="circles"] & {
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 50%;
        padding: 9px;
        height: 1.6em;
        width: 1.6em;
        pfe-card & {
          height: 1.2em;
          width: 1.2em;
        }
      }
      [data-nav-style="text"] &,
      [data-nav-style="circles"] & {
        background: transparent;
        border: 1px solid #252527;
        margin-right: 30px;
        &:last-child {
          margin-right: 0;
        }
        &[data-active="true"],
        &[data-active="false"]:hover {
          background-color: #252527;
          border: 0;
          [data-theme="dark"] & {
            background-color: white;
          }
        }
        [data-theme="dark"] & {
          border-color: white;
        }
      }
      [data-nav-style="arrows"] &[data-active="true"] {
        border-radius: 5px;
        color: #252527;
        [data-theme="dark"] & {
          border-color: white;
        }
        &[data-direction="prev"] {
          border-style: solid;
          border-width: 20px 20px 0;
          border-color: transparent;
          border-top-color: inherit;
          transform: rotate(90deg);
        }
        &[data-direction="next"] {
          border-style: solid;
          border-width: 20px 20px 0;
          border-color: transparent;
          border-top-color: inherit;
          transform: rotate(-90deg);
        }
      }
    }
    &-link {
      transition-property: color 0.2s ease-in 0s;
      [data-nav-style="text"] &,
      [data-nav-style="circles"] & {
        text-decoration: none;
        text-transform: uppercase;
        font-weight: 600;
        text-align: center;
        font-size: 0.9em;
        @media (min-width: 768px) {
          font-size: 1em;
        }
      }
      [data-active="true"] &,
      [data-active="false"] & {
        color: white;
        [data-theme="dark"] & {
          color: #252527;
        }
      }
    }
  }
}

.switcher {
  padding-top: 0;
  overflow: hidden;
  position: relative;
  &-body {
    transition: min-height 1s ease;
  }
  &-tab {
    height: 0;
    overflow: hidden;
    &[data-active="true"] {
      height: auto;
    }
  }
  &-slide {
    position: absolute;
    top: 0;
    left: 0;
    /* set up the transitions */
    transition: -webkit-transform 1s ease;
    transition: transform 1s ease;
    transition: transform 1s ease, -webkit-transform 1s ease;
    &[data-state="current"] {
      transform: translate(0, 0);
      transform: translate3D(0, 0, 0);
    }
    &[data-state="next"] {
      transform: translate(200%, 0);
      transform: translate3D(200%, 0, 0);
    }
    &[data-state="prev"] {
      transform: translate(-150%, 0);
      transform: translate3D(-150%, 0, 0);
    }
  }
}

/* Magnific Popup CSS */
.mfp * {
  cursor: zoom-in;
}

.mfp-zoom-out-cur {
  cursor: zoom-out;
}

.mfp-zoom-in .mfp-with-anim {
  opacity: 0;
  transition: all 0.2s ease-in-out;
  transform: scale(0.8);
}

.mfp-zoom-in.mfp-bg {
  opacity: 0;
  transition: all 0.3s ease-out;
}

.mfp-zoom-in.mfp-ready.mfp-bg {
  opacity: 0.8;
}

.mfp-zoom-in.mfp-ready .mfp-with-anim {
  opacity: 1;
  transform: scale(1);
}

.mfp-zoom-in.mfp-removing.mfp-bg {
  opacity: 0;
}

.mfp-zoom-in.mfp-removing .mfp-with-anim {
  transform: scale(0.8);
  opacity: 0;
}

.mfp-figure {
  background-color: rgba(37, 37, 39, 0.8);
}

.mfp-img {
  width: 100%;
  height: auto;
  max-height: none !important;
}

.mfp-bg {
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1042;
  overflow: hidden;
  position: fixed;
  background: #0b0b0b;
  opacity: 0.8;
}

.mfp-wrap {
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1043;
  position: fixed;
  outline: none !important;
}

.mfp-container {
  text-align: center;
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  padding: 0 8px;
  box-sizing: border-box;
}

.mfp-container:before {
  content: "";
  display: inline-block;
  height: 100%;
  vertical-align: middle;
}

.mfp-align-top .mfp-container:before {
  display: none;
}

.mfp-content {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  margin: 0 auto;
  text-align: left;
  z-index: 1045;
}

.mfp-inline-holder .mfp-content,
.mfp-ajax-holder .mfp-content {
  width: 100%;
  cursor: auto;
}

.mfp-auto-cursor .mfp-content {
  cursor: auto;
}

.mfp-s-error .mfp-content {
  display: none;
}

.mfp-iframe-holder .mfp-content {
  line-height: 0;
  width: 100%;
  max-width: 900px;
}

.mfp-image-holder .mfp-content {
  max-width: 100%;
}

.mfp-ajax-cur {
  cursor: progress;
}

.mfp-zoom {
  cursor: pointer;
  cursor: zoom-in;
}

.mfp-zoom-out-cur,
.mfp-zoom-out-cur .mfp-image-holder .mfp-close {
  cursor: zoom-out;
}

.mfp-close,
.mfp-arrow,
.mfp-preloader,
.mfp-counter {
  user-select: none;
}

.mfp-loading.mfp-figure {
  display: none;
}

.mfp-hide {
  display: none !important;
}

.mfp-preloader {
  color: #ccc;
  position: absolute;
  top: 50%;
  width: auto;
  text-align: center;
  margin-top: -0.8em;
  left: 8px;
  right: 8px;
  z-index: 1044;
}

.mfp-preloader a {
  color: #ccc;
}

.mfp-preloader a:hover {
  color: #fff;
}

.mfp-s-ready .mfp-preloader {
  display: none;
}

.mfp-close,
.mfp-arrow {
  overflow: visible;
  cursor: pointer;
  background: transparent;
  border: 0;
  display: block;
  outline: none;
  padding: 0;
  z-index: 1046;
  box-shadow: none;
  touch-action: manipulation;
}

.mfp-close {
  width: 44px;
  height: 44px;
  line-height: 44px;
  position: absolute;
  right: 0;
  top: 0;
  text-decoration: none;
  text-align: center;
  opacity: 0.65;
  padding: 0 0 18px 10px;
  color: #fff;
  font-style: normal;
  font-size: 28px;
}

.mfp-close:hover,
.mfp-close:focus {
  opacity: 1;
}

.mfp-close:active {
  top: 1px;
}

.mfp-close-btn-in .mfp-close {
  color: #333;
}

.mfp-image-holder .mfp-close,
.mfp-iframe-holder .mfp-close {
  color: #fff;
  right: -6px;
  text-align: right;
  padding-right: 6px;
  width: 100%;
}

.mfp-counter {
  position: absolute;
  top: 0;
  right: 0;
  color: #ccc;
  font-size: 12px;
  line-height: 18px;
  white-space: nowrap;
}

.mfp-arrow {
  position: absolute;
  opacity: 0.65;
  margin: 0;
  top: 50%;
  margin-top: -55px;
  padding: 0;
  width: 90px;
  height: 110px;
}

.mfp-arrow:active {
  margin-top: -54px;
}

.mfp-arrow:hover,
.mfp-arrow:focus {
  opacity: 1;
}

.mfp-arrow:before,
.mfp-arrow::after {
  content: "";
  display: block;
  width: 0;
  height: 0;
  position: absolute;
  left: 0;
  top: 0;
  margin-top: 35px;
  margin-left: 35px;
  border: medium inset transparent;
}

.mfp-arrow::after {
  border-top-width: 13px;
  border-bottom-width: 13px;
  top: 8px;
}

.mfp-arrow:before {
  border-top-width: 21px;
  border-bottom-width: 21px;
  opacity: 0.7;
}

.mfp-arrow-left {
  left: 0;
}

.mfp-arrow-left::after {
  border-right: 17px solid #fff;
  margin-left: 31px;
}

.mfp-arrow-left:before {
  margin-left: 25px;
  border-right: 27px solid #3f3f3f;
}

.mfp-arrow-right {
  right: 0;
}

.mfp-arrow-right::after {
  border-left: 17px solid #fff;
  margin-left: 39px;
}

.mfp-arrow-right:before {
  border-left: 27px solid #3f3f3f;
}

.mfp-iframe-holder {
  padding-top: 40px;
  padding-bottom: 40px;
}

.mfp-iframe-holder .mfp-close {
  top: -40px;
}

.mfp-iframe-scaler {
  width: 100%;
  height: 0;
  overflow: hidden;
  padding-top: 56.25%;
}

.mfp-iframe-scaler iframe {
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.6);
  background: #000;
}

.mfp-figure {
  line-height: 0;
}

.mfp-figure::after {
  content: "";
  position: absolute;
  left: 0;
  top: 40px;
  bottom: 40px;
  display: block;
  right: 0;
  width: auto;
  height: auto;
  z-index: -1;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.6);
  background: #444;
}

.mfp-figure small {
  color: #bdbdbd;
  display: block;
  font-size: 12px;
  line-height: 14px;
}

.mfp-figure figure {
  margin: 0;
}

.mfp-gallery .mfp-image-holder .mfp-figure {
  cursor: pointer;
}

.mfp-bottom-bar {
  margin-top: -36px;
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  cursor: auto;
}

.mfp-title {
  text-align: left;
  line-height: 18px;
  color: #f3f3f3;
  word-wrap: break-word;
  padding-right: 36px;
}

/* Main image in popup */

img.mfp-img {
  width: auto;
  max-width: 100%;
  height: auto;
  display: block;
  line-height: 0;
  box-sizing: border-box;
  padding: 40px 0 40px;
  margin: 0 auto;
}

/* The shadow behind the image */

@media screen and (max-width: 800px) and (orientation: landscape),
  screen and (max-height: 300px) {
  /**
       * Remove all paddings around the image on small screen
       */
  .mfp-img-mobile .mfp-image-holder {
    padding-left: 0;
    padding-right: 0;
  }

  .mfp-img-mobile img.mfp-img {
    padding: 0;
  }

  .mfp-img-mobile .mfp-figure::after {
    top: 0;
    bottom: 0;
  }

  .mfp-img-mobile .mfp-figure small {
    display: inline;
    margin-left: 5px;
  }

  .mfp-img-mobile .mfp-bottom-bar {
    background: rgba(0, 0, 0, 0.6);
    bottom: 0;
    margin: 0;
    top: auto;
    padding: 3px 5px;
    position: fixed;
    box-sizing: border-box;
  }

  .mfp-img-mobile .mfp-bottom-bar:empty {
    padding: 0;
  }

  .mfp-img-mobile .mfp-counter {
    right: 5px;
    top: 3px;
  }

  .mfp-img-mobile .mfp-close {
    top: 0;
    right: 0;
    width: 35px;
    height: 35px;
    line-height: 35px;
    background: rgba(0, 0, 0, 0.6);
    position: fixed;
    text-align: center;
    padding: 0;
  }
}

@media all and (max-width: 900px) {
  .mfp-arrow {
    transform: scale(0.75);
  }

  .mfp-arrow-left {
    transform-origin: 0;
  }

  .mfp-arrow-right {
    transform-origin: 100%;
  }

  .mfp-container {
    padding-left: 6px;
    padding-right: 6px;
  }
}

